.projects-grid{
  display: flex;
}

.contact-body{
  /*margin: auto;*/
  /*position: fixed;*/
  width: 100%;
 /* height: 100%;*/
  justify-content: center;
  padding-top: 2em; 
}

.contact-grid {
  text-align: center;
  justify-content: center;
  width: 80%;
  height: 550px;
  background: white;
  color: black;
  margin-bottom: 20px;
}

.contact-grid h2 {
  font-family: 'Anton', sans-serif;
}

.contact-grid p {
  font-family: 'Oxygen', sans-serif;
  font-weight: bold;
}

.contact-grid hr{
  border-top: 5px dotted black;
  width: 50%;
  margin: auto;
}

.contact-list i {
  font-size: 60px;
  padding-right: 5rem;

}

.resume-right-col{
  background: #27221F;
  color: white;
  padding: 2em;

}

.banner-text{
background-color: black;
opacity: .8;
width: 75%;
margin: auto;
border-radius: 10px;
}

.banner-text h1{
  font-size: 66px;
  font-weight: bold;
  color: white;

}

.banner-text hr{
  border-top: 5px dotted white;
  width: 50%;
  margin: auto;


}

.banner-text p {
  color: white;
  font-size: 20px;
  padding: 1em;
}

.social-links {
 display: flex;
 justify-content: space-between;
 width: 50%;
 margin: auto;
}

.social-links i {
  color: white;
  font-size: 5em;
  padding: 8px;
}


.App {
  text-align: center;
}

.App-logo {
  animation: App-logo-spin infinite 20s linear;
  height: 40vmin;
}

.App-header {
  background-color: #282c34;
  min-height: 100vh;
  display: flex;
  flex-direction: column;
  align-items: center;
  justify-content: center;
  font-size: calc(10px + 2vmin);
  color: white;
}

.App-link {
  color: #61dafb;
}

@keyframes App-logo-spin {
  from {
    transform: rotate(0deg);
  }
  to {
    transform: rotate(360deg);
  }
}
